<template>
  <div class="hello">
    <div class="top_div" :style="{backgroundImage: 'url(' + bg + ')' }"></div>

    <div class="list_div">
      <div class="list" v-for="(item, index) in listData" :key="index">
        <div class="top dsp_flex flex_verticalCenter">
          <img src="../assets/img/note.png" alt="" />
          <span class="txt_ellipsis">{{ item.title }}</span>
        </div>

        <div class="bottom">
          <div class="salary_div">
            <span>{{ item.salary }}</span>
          </div>

          <div class="salaryMaxMin_div dsp_flex">
            <span>综合薪资:&nbsp;</span>
            <span>{{ item.salaryMin }}-{{ item.salaryMax }}元</span>
            <span class="carSubsidy_span">车补: {{ item.carSubsidy }}元</span>
          </div>

          <div class="mark_div">
            <span>备注: {{ item.mark }}</span>
          </div>

          <div class="welfare_div">
            <span v-for="(item, index) in labelArr[index]" :key="index">{{
              item
            }}</span>                        
          </div>

          <div class="richTxt_div">
            <span v-html="richTxtFilterFn(item.content)"></span>
          </div>
        </div>
      </div>
    </div>

    <div
      class="logo_div1"
      :class="listData.length >= 2 ? 'logo_div' : ''"
    >
      <img class="footer_logo" src="../assets/img/logo_bm.png" alt="" />
    </div>
  </div>
</template>

<script>
import { Indicator } from "mint-ui";
import { Toast } from "mint-ui";
import bg from '../../static/img/bg.png'

export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      listData: [],
      labelArr: [],
      bg: bg
    };
  },
  mounted() {
    this.getList();
  },
  methods: {
    getList() {
      Indicator.open();

      this.$http.get("api/bmPromotionConfig/getList").then((res) => {
        Indicator.close();

        if (res.data.code === 10000) {
          this.listData = res.data.data;

          let labelArr = [];
          
          res.data.data.forEach((item) => {            
            if(item.label){
              labelArr.push(item.label.split(","));
            }
          }) 
        
          this.labelArr = labelArr;
        } else {
          Toast({
            message: "接口错误",
            position: "bottom",
            duration: 5000,
          });
        }
      });
    },

    richTxtFilterFn(data) {
      return data.replace(/(\r\n|\n|\r)/gm, "<br />");
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



